.contact__container {
  grid-template-columns: repeat(2, max-content);
  justify-content: center;
  column-gap: 6rem;
  padding-bottom: 3rem;
}

.contact__title {
  text-align: center;
  font-size: var(--h3-font-size);
  font-weight: var(--font-medium);
  margin-bottom: var(--mb-1-5);
}

.contact__info {
  display: grid;
  row-gap: 1rem;
  grid-template-columns: 300px;
}

.contact__card,
.contact__card_dark {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 1rem;
  border-radius: 0.75rem;
  text-align: center;
}
.contact__card_dark {
  border: 1px solid var(--text-color) !important;
}

.contact__card-icon {
  font-size: 2rem;
  color: var(--title-color);
  margin-bottom: var(--mb-0-25);
}

.contact__card-title,
.contact__card-data {
  font-size: var(--small-font-size);
}

.contact__card-title {
  font-weight: var(--font-medium);
}

.contact__card-data {
  display: block;
  margin-bottom: var(--mb-0-75);
  color: var(--text-color);
}

.contact__button {
  color: var(--text-color);
  font-size: var(--small-font -size);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  column-gap: 0.25rem;
}

.contact__button-icon {
  font-size: 1rem;
  transition: 0.3s;
}

.contact__button:hover .contact__button-icon {
  transform: translate(0.25rem);
}

.contact__form {
  width: 360px;
}

.contact__form-div {
  position: relative;
  margin-bottom: var(--mb-2);
  height: 4rem;
}

.contact__form-input {
  border: 2px solid rgba(0, 0, 0, 0.3);
}
.contact__form-input,
.contact__form-input-dark {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: none;
  color: var(--text-color);
  outline: none;
  border-radius: 0.75rem;
  padding: 1.5rem;
  z-index: 1;
}

.contact__form-tag,
.contact-form_tag-dark {
  position: absolute;
  top: -0.75rem;
  left: 1.25rem;
  font-size: var(--smaller-font);
  padding: 0.25rem;
  background-color: var(--body-color);
  z-index: 10;
}
.contact-form_tag-dark {
  background-color: var(--dark-mode) !important;
}

.contact__form-area {
  height: 11rem;
}

.contact__form-area textarea {
  resize: none;
}

.toast_message {
  z-index: 10;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 60px;
  border-radius: 8px;
  background-color: green;
  color: #fff;
  position: fixed;
  bottom: 10px;
  right: 30px;
}
.warning{
  background-color: red;

}
.toast-content {
  width: 100%;
  height: 100%;
  gap: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.toast-cross {
  position: absolute;
  right: 12px;
  font-size: 20px;
  cursor: pointer;
}

/*=============== BREAKPOINTS ===============*/

/* For large devices */
@media screen and (max-width: 992px) {
  .contact__container {
    column-gap: 3rem;
  }
}

/* For medium devices */
@media screen and (max-width: 768px) {
  .toast_message {
    font-size: 14px;
    width: 250px;
    height: 50px;
    border-radius: 6px;
    bottom: 70px;
    right: 20px;
  }

  .toast-cross {
    font-size: 16px;
  }
  .contact__container {
    grid-template-columns: 1fr;
    row-gap: 3rem;
  }
  .contact__info {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
  }
  .contact__card {
    width: 300px;
  }

  .contact__form {
    margin: 0 auto;
  }
}

@media screen and (max-width: 576px) {
  .contact__form {
    width: 100%;
  }
}
