.header {
  position: relative;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
}
.header-dark {
  background-color: var(--dark-mode);
}

.nav {
  height: calc(var(--header-height) + 1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
}

.nav__logo,
.nav__toggle {
  color: var(--title-color);
  font-size: var(--font-medium);
}
.nav-toggle-active {
  color: var(--dark-mode-text-active) !important;
}

.nav__list {
  display: flex;
  column-gap: 2rem;
}
.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  transition: 0.3s;
}
.nav-link-dark {
  color: var(--container-color) !important;
}
.nav-link-dark-active {
  color: var(--dark-mode-text-active) !important;
}

.nav__icon,
.nav__close,
.nav__toggle {
  display: none;
}

/* Active link */
.active-link,
.nav__link:hover {
  color: var(--title-color-dark);
}

.toggle_switch {
  position: absolute;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
}

/*=============== BREAKPOINTS ===============*/

/* For medium devices */
@media screen and (max-width: 768px) {
  .header {
    top: initial;
    bottom: 0;
  }
  .nav {
    height: var(--header-height);
  }
  .nav__menu,
  .nav_menu_dark {
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: 0.3s;
  }
  .nav_menu_dark {
    box-shadow: -4px 5px 7px 0px rgba(0, 0, 0, 0.8) !important;
    background-color: hsl(0deg 4.84% 7.51%) !important;
  }

  /* Show Menu */
  .show-menu {
    bottom: 0;
  }

  .nav__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
  .nav__icon {
    font-size: 1.2rem;
  }
  .nav__close {
    position: absolute;
    right: 1.3rem;
    bottom: 0.5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--title-color);
  }
  .nav__close:hover {
    color: var(--title-color-dark);
  }
  .nav__toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }
  .nav__icon,
  .nav__close,
  .nav__toggle {
    display: block;
  }
  .nav-link-dark {
    color: var(--title-color) !important;
  }
  .nav-link-dark-active {
    color: var(--title-color) !important;
  }
}

/* For small devices */
@media screen and (max-width: 350px) {
  .nav__menu {
    padding: 2rem 0.25rem 4rem;
  }
  .nav__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    column-gap: 0;
  }
  .nav-link-dark {
    color: var(--title-color) !important;
  }
  .nav-link-dark-active {
    color: var(--title-color) !important;
  }
}
@media screen and (max-width: 768px) {
  .toggle_switch {
    right: 40%;
    top: 50%;
  }
}
